import React, { Component } from "react";
import { Link } from "react-router-dom";
import HeaderCard from "../HeaderCard";
import FormPembayaranHutangSupplier from "./Component/FormPembayaranHutangSupplier";

class PembayaranHutangSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handlSubmit(data) {
    const isi = {
      tanggal: data.tanggal,
      supplier: data.supplier,
      noBon: data.noBon,
      detial: data.detail,
    };
    console.log(isi);
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiSupplier">
            <button type="button" className="btn btn-secondary bg-dark">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="col-lg-12 col-d-12">
          <div className="card">
            <HeaderCard title="Pembayaran Hutang Supplier" />
            <div className="card-body">
              <FormPembayaranHutangSupplier
                onSubmit={(data) => this.handlSubmit(data)}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PembayaranHutangSupplier;
